import Link from 'next/link'

const linkStyle = {
  marginRight: 15
}

const contentM = {
  float: 'left',
  width: '260px',
  backgroundColor: 'red'
}

const Sidebar = () => (
    <div style={contentM}>
      <h1>Menu</h1>
      <ul>
        <li>
          <Link href="/">
            <a style={linkStyle}>Truc</a>
          </Link>
        </li>
        <li>
          <Link href="/">
            <a style={linkStyle}>Truc2</a>
          </Link>
        </li>
      </ul>
    </div>
)

export default Sidebar



